/* container styles */
.tooltip {
  position: relative;
  display: inline-block;
  cursor: pointer;
  /* tooltip text */
  /* show when hover */
}
.tooltip .tooltip-text {
  visibility: hidden;
  width: auto;
  background-color: #666666;
  color: #f8f8f8;
  text-align: center;
  padding: 0.5rem;
  border-radius: 0.5rem;
  font-size: 1.3rem;
  /* Posición relativa al elemento */
  position: absolute;
  z-index: 10;
  top: 125%; /* move below the button */
  left: 50%;
  transform: translateX(-50%);
  /* tooltip arrow */
  /* transition effect */
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
.tooltip .tooltip-text::after {
  content: "";
  position: absolute;
  bottom: 100%; /* over the tooltip */
  left: 50%;
  transform: translateX(-50%);
  border-width: 5px;
  border-style: solid;
  border-color: #666666 transparent transparent transparent;
}
.tooltip:hover .tooltip-text {
  visibility: visible;
  opacity: 1;
}

.navbar {
  position: fixed; /* Fija el navbar */
  top: 0; /* Ubicación: parte superior de la pantalla */
  left: 0; /* Ubicación: alineado al lado izquierdo */
  width: 100%; /* Ocupa todo el ancho */
  z-index: 100;
  background-color: #000000;
  color: #f8f8f8;
  padding: 8px;
  /* hidden content */
}
.navbar .container {
  position: relative;
  justify-content: space-between;
}
.navbar .dropdown-container {
  position: relative;
}
.navbar .logo:hover {
  opacity: 0.85;
}
.navbar .logo img {
  border-radius: 16px;
  height: 5rem;
}
.navbar .dropdown {
  position: relative;
}
.navbar .dropdown-button {
  font-size: 1.6rem;
  border: none;
  cursor: pointer;
  width: 4rem;
  border-radius: 50%;
  margin: auto;
}
.navbar .dropdown-button:hover {
  opacity: 0.9;
}
.navbar .dropdown-content {
  display: none;
  position: absolute;
  background-color: #f8f8f8;
  min-width: 15rem;
  max-width: 15rem;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  z-index: 10;
  bottom: 0;
  left: -15rem;
}
.navbar .dropdown-content.show {
  display: block;
}
.navbar .dropdown-content a {
  color: #000000;
  padding: 16px;
  display: block;
}
.navbar .dropdown-content a:hover {
  background-color: #e1e1e1;
}
.navbar .user-info {
  position: absolute;
  top: 5rem;
  left: -7rem;
  min-width: 13rem;
  background: #000000;
  color: #e1e1e1;
  border-radius: 0 0 1rem 1rem;
  max-height: 4rem;
  overflow: hidden;
}
.navbar .user-info p {
  text-align: center;
  margin-bottom: 0;
  font-size: 1.2rem;
}

/* Estilos generales del footer */
.footer {
  background-color: #333333;
  color: #f8f8f8;
  padding: 6rem 3rem;
  text-align: center;
  /* Sección inferior del footer */
}
.footer .footer-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .footer .footer-container {
    grid-template-columns: repeat(2, 1fr);
  }
}
.footer .footer-title {
  margin-bottom: 2rem;
  font-size: 1.8rem;
  color: #ffd700;
}
.footer .footer p,
.footer .footer ul,
.footer .footer a {
  font-size: 1.4rem;
  line-height: 1.5;
}
.footer .footer a {
  color: #f8f8f8;
  text-decoration: none;
}
.footer .footer a:hover {
  text-decoration: underline;
}
.footer .footer-links ul {
  list-style: none;
  padding: 0;
}
.footer .footer-links li {
  margin: 0.5rem 0;
}
.footer .footer-bottom {
  margin-top: 6rem;
  border-top: 1px solid #e1e1e1;
  padding-top: 1rem;
  font-size: 1.2rem;
  color: #e1e1e1;
}

/* Banner */
.banner {
  position: relative;
  height: 100dvh;
  margin-top: 5rem;
  background-size: cover;
  background-position: center;
  color: #f8f8f8;
  text-align: center;
  overflow: hidden;
  box-shadow: inset 0 0 150px rgba(0, 0, 0, 0.7);
  padding: 16px;
}
.banner.img-1 {
  background-image: url("/build/img/home/banner-img1.webp");
}
.banner.img-2 {
  background-image: url("/build/img/home/banner-img2.webp");
}
.banner.img-3 {
  background-image: url("/build/img/home/banner-img3.webp");
}
.banner.img-4 {
  background-image: url("/build/img/home/banner-img4.webp");
}
.banner.img-5 {
  background-image: url("/build/img/home/banner-img5.webp");
}
.banner.img-6 {
  background-image: url("/build/img/home/banner-img6.webp");
}

.banner.hidden {
  opacity: 0; /* Estado inicial al cambiar */
}

/* Banner content */
.banner-content {
  max-width: 70rem;
  padding: 32px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(6px);
}

.banner-title {
  font-size: 3rem;
  font-weight: 700;
  color: #ffd700;
  margin-bottom: 2rem;
}

.banner-subtitle {
  font-size: 1.9rem;
  color: #e1e1e1;
  margin-bottom: 3rem;
  font-weight: 700;
}

.banner-button {
  display: block;
  width: 15rem;
  margin: 0 auto;
  font-size: 1.4rem;
  color: #000000;
  background-color: #ffd700;
  padding: 1rem 1.5rem;
  border: none;
  border-radius: 5rem;
  cursor: pointer;
  font-weight: 700;
  box-shadow: 0 5px 15px rgba(255, 215, 0, 0.3);
  transition: transform 0.3s, box-shadow 0.3s;
}

.banner-button:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(255, 215, 0, 0.5);
}

.cards-section {
  padding: 10rem 0 20rem;
}
.cards-section h2 {
  text-align: center;
  font-size: 3rem;
  color: #000000;
  margin-bottom: 6rem;
}
.cards-section .grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 4rem;
}
@media (min-width: 768px) {
  .cards-section .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
.cards-section .grid-item {
  background-color: #e63946;
  color: #f8f8f8;
  padding: 2rem;
  border-radius: 1rem;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.cards-section .grid-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}
.cards-section .grid-item i {
  font-size: 6rem;
  margin-bottom: 2rem;
  display: block;
}
.cards-section .grid-item h3 {
  font-size: 2.2rem;
  margin-bottom: 1rem;
}
.cards-section .grid-item p {
  font-size: 1.8rem;
}